import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/services',
    name: 'Services',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Services.vue')
  },
  {
    path: '/portfolio',
//    name: 'Portfolio',
    component: () => import(/* webpackChunkName: "about" */ '../views/Portfolio.vue'),
    children: [
      {
        path: '',
        name: 'Works',
        component: () => import( /* webpackChunkName: "about" */ '../components/Works.vue'),
      },
      {
        path: '/html',
        name: 'Html',
        component: () => import( /* webpackChunkName: "about" */ '../components/Html.vue'),
      },
      {
        path: '/print',
        name: 'Print',
        component: () => import(/* webpackChunkName: "about" */ '../components/Print.vue'),
      },
      {
        path: '/identify',
        name: 'Identify',
        component: () => import(/* webpackChunkName: "about" */ '../components/Identify.vue'),
      },
      {
        path: '/branding',
        name: 'Branding',
        component: () => import(/* webpackChunkName: "about" */ '../components/Branding.vue'),
      },
      {
        path: '/web',
        name: 'Web',
        component: () => import(/* webpackChunkName: "about" */ '../components/Web.vue'),
      },
      {
        path: '/wordpress',
        name: 'Wordpress',
        component: () => import(/* webpackChunkName: "about" */ '../components/Wordpress.vue'),
      }
    ]
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import(/* webpackChunkName: "about" */ '../views/Contact.vue')
  },
  {
    path: '/information',
    name: 'MoreInformation',
    component: () => import(/* webpackChunkName: "about" */ '../views/MoreInformation.vue')
  },
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
  scrollBehavior (to) {
    if (to.hash && to.hash !== '#') {
      return {
        selector: to.hash
      }
    }

    return { x: 0, y: 0 }
  }
})

export default router
